import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys } from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";


import "./dashboard.css"
import { AddClient_Cmpt, ViewClient_Cmpt, ClientItem_Cmpt } from "..";
import { OpenModal } from "../../../components/DialogHRM";
import { search_notary } from "../services/fetch_service";



const SearchInput = SearchInput_Cmpt();
const AddClient = AddClient_Cmpt();

const UpdClient = ViewClient_Cmpt();

const ClientItem = ClientItem_Cmpt();





const ClientDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [addview, setAddview] = useState(false);
	const [updview, setUpdview] = useState(false);
	const [loading, set_loading] = useState(false);



	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
		}
	});


	const callbackSearch =  () => {
		set_obs(gen16CodeId())
		setTimeout(()=>set_loading(false),50);
	}

	const search_client = async (f, v) => {
		set_loading(true)
		search_notary(callbackSearch);
	};

	const openUpdateView =  () => {
		setAddview(false)
		setUpdview(true)
	}
	const openAddView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<AddClient />
		
		OpenModal(modal_data);
	}


	const handleChange =  (v) => {
		!loading && set_loading(true)
	}

	return (

	<div className="">
		<div className="clients_header">
			<h2>Clientes</h2>
			<div className="_dsplFlx ">
				<SearchInput
					label={"Buscar un cliente"} 
					width={360}
					obs={set_obs}
					fetchData={search_client}
					loading={loading}
					change={handleChange}
					keyFlds={"searchQry"}
				/>
				<p className="group_title addBtn purple" onClick={openAddView}>
					Agregar Cliente
				</p>
			</div>
		</div>

		{addview && 
			<AddClient close={()=>setAddview(false)}/>
		}

		{updview && 
			<UpdClient close={()=>setUpdview(false)}/>
		}
		
		{!updview && !addview && 
			
			<div className="clients_container">
				{getStore("searchQry")?.length>0 && getStore("client_list") && ObjectKeys(getStore("client_list"))?.map(cid=>{
					return (
						<ClientItem 
							data={getStore("client_list")[cid]} 
							elmId={cid} 
							key={cid}
							openUpdateView={openUpdateView}
						/>
					)
				})}
			</div>
		}
		</div>
	);
};

export default ClientDashboard;

